body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
        'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
        'Helvetica Neue', sans-serif;
    scroll-behavior: smooth;
}
.bottom_nav {
    display: none;
    position: fixed;
    bottom: 0;
    z-index: 1;
}
.btn {
    border: none;
    background-color: transparent;
    outline: none;
    color: red;
}

@media only screen and (max-width: 480px) {
    .hide {
        display: none;
    }
    .icon-size {
        font-size: 10px;
    }
    .bottom_nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        overflow: hidden;
    }
    .is-center {
        text-align: center;
    }
    .mobile_width {
        width: 80%;
    }
    .is-flex {
        display: flex;
        justify-content: center;
    }
    .mobile_mt {
        margin-top: 5%;
    }
    .mobile_pb {
        padding-bottom: 22px;
    }
    .mobile_pl {
        padding-left: 15px;
    }
    .mobile_text {
        font-size: 14px;
        font-weight: 500;
    }
}
